
'use client';
import * as React from 'react';
import Header from "@/components/layout/header";
import Footer from "@/components/layout/footer";
import SocialBar from "@/components/layout/social-bar";
import HeroSection from "@/components/sections/hero-section";
import AboutSection from "@/components/sections/about-section";
import LocationSection from '@/components/sections/location-section';

export default function Home() {
  const [mousePosition, setMousePosition] = React.useState({ x: -1000, y: -1000 });

  const handleMouseMove = (e: React.MouseEvent<HTMLElement>) => {
      setMousePosition({ x: e.clientX, y: e.clientY });
  };
  
  return (
    <div 
      className="flex min-h-screen flex-col relative group"
      onMouseMove={handleMouseMove}
    >
       <div 
            className="pointer-events-none fixed inset-0 z-50 transition-all duration-300 opacity-0 group-hover:opacity-100"
            style={{
                background: `radial-gradient(250px at ${mousePosition.x}px ${mousePosition.y}px, rgba(209, 163, 84, 0.3), transparent 80%)`,
            }}
        />
      <Header />
      <SocialBar />
      <main className="flex-1">
        <HeroSection />
        <AboutSection />
        <LocationSection />
      </main>
      <Footer />
    </div>
  );
}
